<template>
  <div class="movie-list-grid" v-if="movieLists.length > 0">
    <div class="movie-card" v-for="movie in movieLists" :key="movie.id">
      <Moviecard :types="typeList" :movie="movie" />
    </div>
  </div>
  <div v-else class="no-movies">
    <p class="text-center">Nothing found...</p>
  </div>
</template>

<script>
import Moviecard from "./Moviecard";

export default {
  name: "Movielist",
  props: ["movieLists", "typeList"],
  components: {
    Moviecard,
  },
};
</script>

<style>
.movie-list-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 10px 10px;
}

.no-movies {
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 450px) {
  .movie-list-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 600px) {
  .movie-list-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 1100px) {
  .movie-list-grid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 25px;
  }
}
</style>