<template>
  <div class="home">
    <Navigationbtns />
    <Search @search="searchMovie" />
    <Movielist :types="typeList" v-if="list" :movieLists="list" />
    <div v-else>loading...</div>
  </div>
</template>

<script>
import Search from "../components/Search.vue";
import Movielist from "../components/Movielist.vue";
import Navigationbtns from "../components/Navigationbtns.vue";
import axios from "axios";

export default {
  name: "Home",
  props: ["movielist"],
  components: {
    Search,
    Movielist,
    Navigationbtns
  },
  data() {
    return {
      list: [],
      apiKey: "9989f1431c1da964ba5f3f0e5931d4c4",
      stype: "tv",
      typeList: "tv",
    };
  },
  methods: {
    async loadMovies() {
      try {
        let res = await axios
          .get(`https://api.themoviedb.org/3/tv/popular?api_key=${this.apiKey}`)
          .then((result) => {
            return result;
          });
        return res.data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  async created() {
    let { results } = await this.loadMovies();
    console.log(results);
    this.list = results;
    this.$store.commit('loadStateUpdate', false);
    this.$emit("headerText", "Populer Tv Shows");
  },
  beforeCreate() {
    this.$store.commit('loadStateUpdate', true);
  }
};
</script>
