<template>
  <div class="movie">
    <Backtohome :link="backLink" />
    <h3 style="margin: 20px">Search results for '{{ searchKey }}'</h3>
    <Movielist :typeList="typeList" v-if="list" :movieLists="list" />
    <div v-else>loading...</div>
  </div>
</template>

<script>
import Movielist from "../components/Movielist.vue";
import Backtohome from "../components/Backtohome.vue";
import axios from "axios";

export default {
  name: "Tvshows",
  props: ["movielist"],
  components: {
    Movielist,
    Backtohome
  },
  data() {
    return {
      list: [],
      typeList: "tv",
      backLink: "/tv",
      searchKey: ""
    };
  },
  methods: {
    async searchMovie(keyword) {
      try {
        let response = await axios
          .get(
            `https://api.themoviedb.org/3/search/tv?api_key=9989f1431c1da964ba5f3f0e5931d4c4&query=${keyword}`
          )
          .then((result) => {
            return result;
          });
        return response.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
  async created() {
    let routerPage = this.$route.params.name;
    this.searchKey = routerPage;
    let { results } = await this.searchMovie(routerPage);
    this.list = results;
    this.$store.commit('loadStateUpdate', false);
    this.$emit("headerText", "Populer Tv Shows");
  },
  beforeCreate() {
    this.$store.commit('loadStateUpdate', true);
  }
};
</script>
