<template>
  <div class="search-area" :typeSearch="type">
    <input
      type="text"
      name="search-movie"
      class="search-movie"
      placeholder="Search movies..."
      @keyup.enter="getsearch"
      v-model="search"
    />
    <button type="button" name="" class="search-btn" @click="getsearch"><i class="fa fa-search" aria-hidden="true"></i></button>
  </div>
</template>

<script>
export default {
  name: "Search",
  props: ["searchType"],
  data() {
    return {
      search: "",
    };
  },
  methods: {
    getsearch() {
      let searchInput = this.search;
      if (searchInput.length > 0) {
        if(this.searchType == 'movies') {
          this.$router.push(`movies/${searchInput}`);
        } else {
          this.$router.push(`tvshows/${searchInput}`);
        }
        // this.$emit("search", searchInput);
      } else {
        // this.$emit("restore");
      }
    },
  },
};
</script>

<style>
.search-area {
  border-radius: 50px;
  /* padding: 10px; */
  margin: 10px 10px 20px 10px;
  background: #fff;
  box-shadow: 1px 3px 6px 1px #939292;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  height: 50px;
}

.search-area input,
.search-area input:focus,
.search-area input:focus-visible {
  padding: 10px;
  flex: 1;
  border: none;
  box-shadow: none;
  outline: none;
  height: 50px;
  font-size: 1.1rem;
  padding: 0 20px;
}

.search-btn {
  padding: 10px;
  border: none;
  width: 50px;
  background: #333;
  color: #fff;
  cursor: pointer;
  height: 50px;
  font-size: 1.2rem;
}
</style>