<template>
  <div id="cards">
    <div v-if="types === 'movie'">
      <router-link :to="`/moviedetails/${encript(movie.id)}`">
        <div class="image-area">
          <img
            :src="`${imageBaseUrl}${movie.poster_path}`"
            loading="lazy"
            alt="movie-poster"
            class="movie-poster"
          />
        </div>
        <div class="movie-info-area">
          <p class="movie-title">{{ movie.title }}</p>
          <p class="movie-release">
            <span>Relase Date</span>: {{ reverseData(movie.release_date) }}
          </p>
          <p class="movie-ratings">
            <span class="bold">Rating</span>: {{ movie.vote_average }} (<span
              class="small"
              ><span class="bold">Votes</span>: {{ movie.vote_count }}</span
            >)
          </p>
        </div>
      </router-link>
    </div>
    <div v-else>
      <router-link :to="`/tvshowdetails/${encript(movie.id)}`">
        <div class="image-area">
          <img
            :src="`${imageBaseUrl}${movie.poster_path}`"
            loading="lazy"
            alt="movie-poster"
            class="movie-poster"
          />
        </div>
        <div class="movie-info-area">
          <p class="movie-title">{{ movie.name }}</p>
          <p class="movie-release">
            <span>Relase Date</span>: {{ reverseData(movie.first_air_date) }}
          </p>
          <p class="movie-ratings">
            <span class="bold">Rating</span>: {{ movie.vote_average }} (<span
              class="small"
              ><span class="bold">Votes</span>: {{ movie.vote_count }}</span
            >)
          </p>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Moviecard",
  props: ["movie", "types"],
  data() {
    return {
      imageBaseUrl: "https://image.tmdb.org/t/p/original/",
    };
  },
  methods: {
    reverseData(date) {
      let dateArray = date.split("-");
      let newDateArr = dateArray.reverse();
      let newdate = newDateArr.join("-");
      return newdate;
    },
    encript(id) {
      return btoa(id);
    },
  },
};
</script>

<style>
.movie-card {
  box-shadow: 1px 3px 10px 5px #939292;
  border-radius: 7px;
  overflow: hidden;
  background: #fff;
}
.image-area {
  overflow: hidden;
  max-height: 350px;
}

.movie-poster {
  max-width: 100%;
  margin: 0;
  /* height: 350px; */
  max-height: 350px;
  min-height: 350px;
  object-fit: cover;
  width: 100%;
  transition: all 350ms ease;
}

.movie-poster:hover {
  transform: scale(1.1);
}
.movie-info-area {
  padding: 10px;
}

.movie-title {
  text-align: center;
  font-weight: 600;
  font-size: 1.2rem;
  margin-bottom: 10px;
  margin-bottom: 10px;
}

.movie-release {
  text-align: center;
  font-size: 1.1rem;
  margin-bottom: 10px;
}

.movie-release span {
  font-weight: 600;
}

.movie-ratings {
  text-align: center;
  font-size: 1.1 rem;
}

.movie-ratings .bold {
  font-weight: 600;
}

.movie-rating .small {
  font-size: 0.9rem;
}
</style>