<template>
  <div id="movie-detail">
    <Backtohome :link="backLink" />
    <div class="movie-content">
      <div class="movie-image">
        <img
          v-bind:src="`https://image.tmdb.org/t/p/original/${data.poster_path}`"
          alt="movie poster"
          class="movie-poster-image"
        />
      </div>
      <div class="movie-details-block">
        <p class="movie-details-title">{{ data.name }}</p>
        <p class="movie-details-release-date">
          <span>Release Date: </span> {{ reverseDate(data.first_air_date) }}
        </p>
        <p class="movie-details-popularity">
          <span>Popularity: </span> {{ firstTwo(data.popularity) }}%
        </p>
        <p class="movie-details-ratings">
          <span class="bold">Ratings: </span> {{ data.vote_average }} (<span
            class="small"
            ><span class="bold">Votes</span>: {{ data.vote_count }}</span
          >)
        </p>
        <p class="movie-details-budghet">
          <span>Number of seasons: </span> {{ data.number_of_seasons }}
        </p>
        <!-- <p class="movie-details-boxoffice">
          <span>Box Office: </span> {{ data.revenue }} USD
        </p> -->
        <p class="movie-details-category">
          <span>Category: </span>{{ categoryList(data.genres) }}
        </p>
        <p class="movie-details-description-title">Overview:</p>
        <p class="movie-details-detail">{{ data.overview }}</p>
        <div class="buttons">
          <a
            :href="data.homepage"
            v-show="data.homepage"
            target="_blank"
            class="btns"
            >Official Website</a
          >
          <a
            :href="'https://www.imdb.com/title/' + data.imdb_id"
            target="_blank"
            class="btns"
            v-show="data.imdb_id"
            >Imdb Page</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Backtohome from "../components/Backtohome.vue";
// import Loader from "../components/Loader.vue";

export default {
  name: "Tvshowdetails",
  components: {
    Backtohome,
    // Loader,
  },
  data() {
    return {
      id: "",
      data: [],
      apiKey: "9989f1431c1da964ba5f3f0e5931d4c4",
      backLink: "/tv",
      load: true,
    };
  },
  methods: {
    async movieData(id) {
      try {
        let result = await axios
          .get(
            `https://api.themoviedb.org/3/tv/${atob(id)}?api_key=${this.apiKey}`
          )
          .then((result) => {
            return result;
          });

        return result.data;
      } catch (err) {
        console.log(err);
      }
    },
    reverseDate(date) {
      let dateArray = date.split("-");
      let reverseDate = dateArray.reverse();
      let newDate = reverseDate.join("-");
      return newDate;
    },
    firstTwo(popular) {
      let newPop = popular.toString().slice(0, 2);
      return newPop;
    },
    categoryList(category) {
      let cat = [];
      category.forEach((cate) => {
        cat.push(cate.name);
      });
      let categoryList = cat.join(", ");
      return categoryList;
    },
  },
  async created() {
    this.id = this.$route.params.id;
    this.data = await this.movieData(this.id);
    this.$store.commit('loadStateUpdate', false);
    this.$emit("headerText", "Populer Tv Shows");
  },
  beforeCreate() {
    console.log("before craete")
    this.$store.commit('loadStateUpdate', true);
  },
};
</script>

<style>
.movie-content {
  margin: 20px 20px;
  padding: 10px;
  background: #fff;
  box-shadow: 1px 3px 10px 5px #939292;
  border-radius: 7px;
  overflow: hidden;
  min-height: 400px;
}

.movie-poster-image {
  max-width: 100%;
}

.movie-details-block {
  padding: 7px 0 10px 0;
}

.movie-details-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.movie-details-release-date,
.movie-details-popularity,
.movie-details-ratings,
.movie-details-budghet,
.movie-details-boxoffice,
.movie-details-category {
  font-size: 1.1rem;
  margin-bottom: 10px;
}

.movie-details-release-date span,
.movie-details-popularity span,
.movie-details-budghet span,
.movie-details-boxoffice span,
.movie-details-category span {
  font-weight: 600;
  margin-bottom: 10px;
}

.movie-details-description-title {
  font-weight: 600;
  margin-bottom: 10px;
  font-size: 1.1rem;
}

.movie-details-detail {
  font-size: 1.1rem;
  margin-top: 10px;
  margin-bottom: 10px;
}

.movie-details-ratings .bold {
  font-weight: 600;
}

.movie-details-ratings .small {
  font-size: 0.9rem;
}

.buttons {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.btns,
.btns:hover {
  background: #333;
  padding: 10px 30px;
  min-width: 100%;
  color: #fff;
  border-radius: 7px;
  text-transform: uppercase;
  text-align: center;
}

.btns:not(:last-child) {
  margin-bottom: 15px;
}

.back-btn,
.back-btn:hover {
  background: #333;
  padding: 10px 30px;
  border-radius: 7px;
  margin: 0 20px 20px auto;
  display: block;
  width: max-content;
  box-shadow: 2px 2px 10px 1px rgba(0, 0, 0, 0.5);
  color: #fff;
  cursor: pointer;
}

@media screen and (min-width: 600px) {
  .movie-content {
    display: flex;
  }

  .movie-content {
    display: flex;
  }

  .movie-image {
    min-width: 300px;
    max-width: 300px;
  }

  .movie-details-block {
    padding: 0;
    margin-left: 20px;
  }

  .buttons {
    flex-direction: row;
  }

  .btns,
  .btns:hover {
    min-width: 100px;
  }

  .btns:not(:last-child) {
    margin-bottom: 0;
    margin-right: 15px;
  }
}
</style>
